<template>
  <view class="mine-page">
    <!-- 1. Ë¥¶Âè∑‰∏ªÈ°µÔºàÈ°∂ÈÉ®Ôºâ -->
    <view class="user-header">
      <view class="avatar-box">
        <image class="avatar" src="/static/avatar.png" mode="aspectFill"></image>
        <button class="edit-avatar" @click="gotoUserProfile">ÁºñËæëËµÑÊñô</button>
      </view>
      <view class="user-info">
        <text class="username">{{userName}}</text>
        <text class="user-desc">ÂÖ¨ÂÆâÈô¢Ê†°ËøêÂä®ÊâìÂç° ¬∑ {{userType}}</text>
      </view>
      <view class="user-stats">
        <view class="stats-item">
          <text class="stats-num">{{totalRunCount}}</text>
          <text class="stats-text">ÊÄªÊ¨°Êï∞</text>
        </view>
        <view class="stats-item">
          <text class="stats-num">{{totalRunDistance}}km</text>
          <text class="stats-text">ÊÄªË∑ùÁ¶ª</text>
        </view>
        <view class="stats-item">
          <text class="stats-num">{{policeSuccessCount}}</text>
          <text class="stats-text">Ë≠¶Âä°ËææÊ†á</text>
        </view>
      </view>
    </view>

    <!-- 2. Êú¨Âë®Ë∑ëÊ≠•ÁªüËÆ° -->
    <view class="week-run-card">
      <view class="card-header">
        <text class="card-title">Êú¨Âë®Ë∑ëÊ≠•</text>
        <text class="date-range">{{weekDateRange}}</text>
      </view>
      <view class="week-stats">
        <view class="week-item">
          <text class="week-num">{{weekRunCount}}</text>
          <text class="week-text">Ë∑ëÊ≠•Ê¨°Êï∞</text>
        </view>
        <view class="week-item">
          <text class="week-num">{{weekRunDistance}}km</text>
          <text class="week-text">ÊÄªË∑ùÁ¶ª</text>
        </view>
        <view class="week-item">
          <text class="week-num">{{weekPoliceSuccess}}Ê¨°</text>
          <text class="week-text">Ë≠¶Âä°ËææÊ†á</text>
        </view>
      </view>
      <!-- Êú¨Âë®ÁõÆÊ†áËøõÂ∫¶Êù° -->
      <view class="progress-box">
        <text class="progress-title">Êú¨Âë®ÁõÆÊ†áÔºöË∑ëÊ≠•3Ê¨°ÔºàÂÆåÊàê{{weekRunCount}}/3Ôºâ</text>
        <view class="progress-bar">
          <view class="progress-fill" :style="{width: progressPercent + '%'}"></view>
        </view>
      </view>
    </view>

    <!-- 3. ËøêÂä®ËÆ∞ÂΩïÂàóË°® -->
    <view class="record-card">
      <view class="card-header">
        <text class="card-title">ËøêÂä®ËÆ∞ÂΩï</text>
        <button class="view-all" @click="viewAllRecords">Êü•ÁúãÂÖ®ÈÉ®</button>
      </view>
      <!-- ËÆ∞ÂΩïÂàóË°® -->
      <view class="record-list" v-if="runRecords.length > 0">
        <view class="record-item" v-for="(item, index) in showRecords" :key="index" @click="gotoRecordDetail(item)">
          <view class="record-type" :style="{backgroundColor: item.modeBg}">
            <text class="type-text">{{item.modeText}}</text>
          </view>
          <view class="record-info">
            <text class="record-date">{{item.createTime}}</text>
            <text class="record-data" v-if="item.type === 'run'">
              {{item.distance}}km | {{item.duration}}<text v-if="item.pace"> | ÈÖçÈÄüÔºö{{Number(item.pace).toFixed(1)}} ÂàÜ/ÂÖ¨Èáå</text>
            </text>
            <text class="record-data" v-else>{{item.testName}} | Ê¨°Êï∞Ôºö{{item.testCount}} | {{item.result}}</text>
          </view>
          <view class="record-status">
            <text class="status-text" :style="{color: item.statusColor}">{{item.statusText}}</text>
          </view>
        </view>
      </view>
      <!-- Á©∫ËÆ∞ÂΩïÊèêÁ§∫ -->
      <view class="empty-record" v-else>
        <text class="empty-icon">üèÉ</text>
        <text class="empty-text">ÊöÇÊó†ËøêÂä®ËÆ∞ÂΩïÔºåÂø´ÂéªË∑ëÊ≠•ÊâìÂç°ÂêßÔΩû</text>
      </view>
    </view>

    <!-- 4. ËÆæÁΩÆ‰∏≠ÂøÉ -->
    <view class="setting-card">
      <view class="setting-item" @click="gotoDeviceBind">
        <text class="setting-icon">üì±</text>
        <text class="setting-text">ËÆæÂ§áÁªëÂÆöÔºàÈò≤‰ª£Ë∑ëÔºâ</text>
        <text class="setting-desc">{{deviceId || 'Êú™ÁªëÂÆö'}}</text>
        <text class="arrow">Ôºû</text>
      </view>
      <view class="setting-item" @click="clearCache">
        <text class="setting-icon">üóëÔ∏è</text>
        <text class="setting-text">Ê∏ÖÈô§ÁºìÂ≠ò</text>
        <text class="setting-desc">ÈáäÊîæÊú¨Âú∞Â≠òÂÇ®Á©∫Èó¥</text>
        <text class="arrow">Ôºû</text>
      </view>
      <view class="setting-item" @click="gotoAbout">
        <text class="setting-icon">‚ÑπÔ∏è</text>
        <text class="setting-text">ÂÖ≥‰∫éÊàë‰ª¨</text>
        <text class="setting-desc">ÁâàÊú¨ v1.0.0</text>
        <text class="arrow">Ôºû</text>
      </view>
      <view class="setting-item logout" @click="logout">
        <text class="setting-icon">üö™</text>
        <text class="setting-text">ÈÄÄÂá∫ÁôªÂΩï</text>
        <text class="arrow">Ôºû</text>
      </view>
    </view>
    <CustomTabBar current="/pages/mine/mine" />
  </view>
</template>

<script setup>
// Áªü‰∏ÄÂØºÂÖ•ËßÑËåÉ
import { ref, computed} from 'vue';
import { onPullDownRefresh , onShow, onLoad } from '@dcloudio/uni-app';
import CustomTabBar from '@/components/CustomTabBar/CustomTabBar.vue';

// 1. Âü∫Á°ÄÊï∞ÊçÆ
const userName = ref('ËøêÂä®ÂÖàÈîã');
const userType = ref('2024Á∫ß‰æ¶Êü•Á≥ª');
const deviceId = ref('');
const totalRunCount = ref(0);
const totalRunDistance = ref(0);
const policeSuccessCount = ref(0);

// 2. Êú¨Âë®Ë∑ëÊ≠•Êï∞ÊçÆ
const weekRunCount = ref(0);
const weekRunDistance = ref(0);
const weekPoliceSuccess = ref(0);
const weekDateRange = ref('');
const progressPercent = ref(0);

// 3. ËøêÂä®ËÆ∞ÂΩï
const runRecords = ref([]);
const showRecords = ref([]); // Âè™ÊòæÁ§∫ÊúÄËøë5Êù°

// È°µÈù¢Âä†ËΩΩÊó∂ÂàùÂßãÂåñ
onLoad(() => {
  // ÂºÄÂêØ‰∏ãÊãâÂà∑Êñ∞
  uni.startPullDownRefresh();
});

// È°µÈù¢ÊòæÁ§∫Êó∂Âä†ËΩΩÊï∞ÊçÆ
onShow(() => {
  loadUserStats(); // Âä†ËΩΩÁî®Êà∑ÊÄªÊï∞ÊçÆ
  loadWeekData(); // Âä†ËΩΩÊú¨Âë®Êï∞ÊçÆ
  loadRunRecords(); // Âä†ËΩΩËøêÂä®ËÆ∞ÂΩï
  loadDeviceInfo(); // Âä†ËΩΩËÆæÂ§áÁªëÂÆö‰ø°ÊÅØ
});

// ‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
  loadUserStats();
  loadWeekData();
  loadRunRecords();
  // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
  setTimeout(() => {
    uni.stopPullDownRefresh();
    uni.showToast({ title: 'Êï∞ÊçÆÂ∑≤Âà∑Êñ∞', icon: 'success' });
  }, 500);
});

// 4. Âä†ËΩΩÁî®Êà∑ÊÄªÊï∞ÊçÆÔºàÊú¨Âú∞ÁºìÂ≠òÔºâ
const loadUserStats = () => {
  totalRunCount.value = Number(uni.getStorageSync('totalRunCount')) || 0;
  totalRunDistance.value = (Number(uni.getStorageSync('totalRunDistance')) || 0).toFixed(1);
  policeSuccessCount.value = Number(uni.getStorageSync('policeSuccessCount')) || 0;
};

// 5. Âä†ËΩΩÊú¨Âë®Êï∞ÊçÆ
const loadWeekData = () => {
  // ËÆ°ÁÆóÊú¨Âë®Êó•ÊúüËåÉÂõ¥ÔºàÂë®‰∏ÄËá≥Âë®Êó•Ôºâ
  const now = new Date();
  const weekStart = new Date(now.setDate(now.getDate() - now.getDay() + 1));
  const weekEnd = new Date(now.setDate(now.getDate() + 6));
  weekDateRange.value = `${weekStart.getMonth()+1}Êúà${weekStart.getDate()}Êó• - ${weekEnd.getMonth()+1}Êúà${weekEnd.getDate()}Êó•`;

  // Ê®°ÊãüÊú¨Âë®Êï∞ÊçÆÔºàÂÆûÈôÖÂèØ‰ªéÊú¨Âú∞/‰∫ëÁ´ØËØªÂèñÔºâ
  // ËØªÂèñÊâÄÊúâËÆ∞ÂΩïÔºåÁ≠õÈÄâÊú¨Âë®ÁöÑ
  const allRecords = uni.getStorageSync('runRecordsList') || [];
  let weekCount = 0;
  let weekDistance = 0;
  let weekPolice = 0;

  allRecords.forEach(record => {
    const recordTime = new Date(record.createTime);
    // Âà§Êñ≠ÊòØÂê¶Âú®Êú¨Âë®
    if (recordTime >= weekStart && recordTime <= weekEnd) {
      const isRunType = record.type ? record.type === 'run' : true;
      if (isRunType) {
        weekCount++;
        weekDistance += record.distance;
        if (record.mode === 'police' && record.isPaceQualified) {
          weekPolice++;
        }
      } else {
        // ÊµãËØïËÆ∞ÂΩï‰∏çËÆ°ÂÖ•Ë∑ëÊ≠•Âë®ÁªüËÆ°
      }
    }
  });

  weekRunCount.value = weekCount;
  weekRunDistance.value = weekDistance.toFixed(1);
  weekPoliceSuccess.value = weekPolice;
  // ËÆ°ÁÆóËøõÂ∫¶ÔºàÁõÆÊ†á3Ê¨°Ôºâ
  progressPercent.value = Math.min((weekCount / 3) * 100, 100);
};

// 6. Âä†ËΩΩËøêÂä®ËÆ∞ÂΩï
const loadRunRecords = () => {
  // ‰ªéÊú¨Âú∞ÁºìÂ≠òËØªÂèñËÆ∞ÂΩïÔºàÂÆûÈôÖÂèØ‰ªé‰∫ëÁ´ØÊãâÂèñÔºâ
  const records = uni.getStorageSync('runRecordsList') || [];
  // Ê†ºÂºèÂåñËÆ∞ÂΩïÊï∞ÊçÆ
  runRecords.value = records.map(item => {
    // Ê†ºÂºèÂåñÊó∂Èó¥
    const time = new Date(item.createTime);
    const formatTime = `${time.getMonth()+1}Êúà${time.getDate()}Êó• ${time.getHours()}:${time.getMinutes()}`;
    // Ê†ºÂºèÂåñÊó∂Èïø
    const min = Math.floor(item.duration / 60);
    const sec = item.duration % 60;
    const formatDuration = `${min.toString().padStart(2, '0')}:${sec.toString().padStart(2, '0')}`;
    
    // Ê®°ÂºèÈÖçÁΩÆ
    let modeText = '';
    let modeBg = '';
    let statusText = '';
    let statusColor = '';

    const type = item.type || 'run';
    if (type === 'run' && item.mode === 'police') {
      modeText = 'Ë≠¶Âä°‰∏ìÈ°π';
      modeBg = '#fdf2f0';
      if (item.isPoliceFinish && item.isPaceQualified) {
        statusText = 'ËææÊ†áÂÆåÊàê';
        statusColor = '#20C997';
      } else if (item.isPoliceFinish) {
        statusText = 'Êú™ËææÊ†á';
        statusColor = '#d81e06';
      } else {
        statusText = 'Êú™ÂÆåÊàê';
        statusColor = '#ff9500';
      }
    } else if (type === 'run' && item.mode === 'campus') {
      modeText = 'Ê†°Âõ≠ÊâìÂç°';
      modeBg = '#e8f4f8';
      statusText = item.isReach ? 'ÊâìÂç°ÊàêÂäü' : 'ÊâìÂç°Â§±Ë¥•';
      statusColor = item.isReach ? '#20C997' : '#d81e06';
    } else if (type === 'run') {
      modeText = 'ÊôÆÈÄöË∑ëÊ≠•';
      modeBg = '#f5f5f5';
      statusText = 'Â∑≤ÂÆåÊàê';
      statusColor = '#666';
    } else {
      modeText = '‰ΩìËÉΩÊµãËØï';
      modeBg = '#f3f7ff';
      statusText = item.result === 'ÂêàÊ†º' ? 'ÂêàÊ†º' : 'Êú™ÂêàÊ†º';
      statusColor = item.result === 'ÂêàÊ†º' ? '#20C997' : '#d81e06';
    }

    return {
      ...item,
      createTime: formatTime,
      duration: formatDuration,
      modeText,
      modeBg,
      statusText,
      statusColor,
      type
    };
  }).reverse(); // ÂÄíÂ∫èÔºåÊúÄÊñ∞ÁöÑÂú®ÂâçÈù¢

  // Âè™ÊòæÁ§∫ÊúÄËøë5Êù°
  showRecords.value = runRecords.value.slice(0, 5);
};

// 7. Âä†ËΩΩËÆæÂ§á‰ø°ÊÅØ
const loadDeviceInfo = () => {
  deviceId.value = uni.getStorageSync('bindDeviceId') || '';
};

// 8. ‰∫ã‰ª∂Â§ÑÁêÜ
// ÁºñËæëËµÑÊñô/Ë¥¶Âè∑‰∏ªÈ°µ
const gotoUserProfile = () => {
  uni.showToast({ title: 'ÊöÇÊú™ÂºÄÊîæÔºåÊï¨ËØ∑ÊúüÂæÖ', icon: 'none' });
};

// Êü•ÁúãÂÖ®ÈÉ®ËÆ∞ÂΩï
const viewAllRecords = () => {
  if (runRecords.value.length === 0) {
    uni.showToast({ title: 'ÊöÇÊó†ËøêÂä®ËÆ∞ÂΩï', icon: 'none' });
    return;
  }
  // ÂèØË∑≥ËΩ¨Ëá≥ÂÖ®ÈÉ®ËÆ∞ÂΩïÈ°µÈù¢ÔºåËøôÈáåÂÖàÊèêÁ§∫
  uni.showToast({ title: 'Â∑≤Â±ïÁ§∫ÂÖ®ÈÉ®ËÆ∞ÂΩï', icon: 'success' });
};

// Êü•ÁúãËÆ∞ÂΩïËØ¶ÊÉÖ
const gotoRecordDetail = (item) => {
  uni.showModal({
    title: 'ËÆ∞ÂΩïËØ¶ÊÉÖ',
    content: item.type === 'run'
      ? `Ê®°ÂºèÔºö${item.modeText}\nÊó∂Èó¥Ôºö${item.createTime}\nË∑ùÁ¶ªÔºö${item.distance}km\nÊó∂ÈïøÔºö${item.duration}\nÁä∂ÊÄÅÔºö${item.statusText}`
      : `Á±ªÂûãÔºö‰ΩìËÉΩÊµãËØï\nÈ°πÁõÆÔºö${item.testName}\nÊ¨°Êï∞Ôºö${item.testCount}\nÂà§ÂÆöÔºö${item.result}\nÊó∂ÈïøÔºö${item.duration}`,
    showCancel: false
  });
};

const gotoStudentsManage = () => {
  uni.navigateTo({ url: '/pages/teacher/students/students' });
};
const gotoTaskManage = () => {
  uni.showToast({ title: 'ÊïôÂ≠¶‰ªªÂä°ÁÆ°ÁêÜÂºÄÂèë‰∏≠', icon: 'none' });
};
const gotoExportCenter = () => {
  uni.showToast({ title: 'ÂØºÂá∫‰∏≠ÂøÉÂºÄÂèë‰∏≠', icon: 'none' });
};

const handleTeacherMineAction = (action) => {
  if (action === 'ÂæÖÂÆ°Êâπ‰ªªÂä°') {
    uni.showToast({ title: 'Ë∑≥ËΩ¨Ëá≥ÂÆ°ÊâπÂàóË°®', icon: 'none' });
  } else if (action === 'Â≠¶ÂëòËÄÉËØïËÆ∞ÂΩï') {
    uni.showToast({ title: 'Ë∑≥ËΩ¨Ëá≥ÊàêÁª©Êü•ËØ¢', icon: 'none' });
  }
};

// ËÆæÂ§áÁªëÂÆö
const gotoDeviceBind = () => {
  if (deviceId.value) {
    // Â∑≤ÁªëÂÆöÔºåÁ°ÆËÆ§ÊòØÂê¶Ëß£Áªë
    uni.showModal({
      title: 'ËÆæÂ§áÁªëÂÆö',
      content: 'ÊòØÂê¶Ëß£Èô§ÂΩìÂâçËÆæÂ§áÁªëÂÆöÔºü',
      success: (res) => {
        if (res.confirm) {
          uni.removeStorageSync('bindDeviceId');
          deviceId.value = '';
          uni.showToast({ title: 'Â∑≤Ëß£Èô§ÁªëÂÆö', icon: 'success' });
        }
      }
    });
  } else {
    // Êú™ÁªëÂÆöÔºåÊâßË°åÁªëÂÆö
    uni.getSystemInfo({
      success: (res) => {
        const uniqueId = res.deviceId || `${res.platform}_${res.model}`;
        uni.setStorageSync('bindDeviceId', uniqueId);
        deviceId.value = uniqueId;
        uni.showToast({ title: 'ËÆæÂ§áÁªëÂÆöÊàêÂäü', icon: 'success' });
      },
      fail: () => {
        uni.showToast({ title: 'Ëé∑ÂèñËÆæÂ§á‰ø°ÊÅØÂ§±Ë¥•', icon: 'none' });
      }
    });
  }
};

// Ê∏ÖÈô§ÁºìÂ≠ò
const clearCache = () => {
  uni.showModal({
    title: 'Ê∏ÖÈô§ÁºìÂ≠ò',
    content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÈô§Êú¨Âú∞ÁºìÂ≠òÂêóÔºüËøêÂä®ËÆ∞ÂΩï‰∏ç‰ºöË¢´Âà†Èô§',
    success: (res) => {
      if (res.confirm) {
        // Ê∏ÖÈô§ÈùûËÆ∞ÂΩïÁ±ªÁºìÂ≠ò
        uni.removeStorageSync('checkpoint');
        uni.removeStorageSync('policeFinishTip');
        uni.showToast({ title: 'ÁºìÂ≠òÂ∑≤Ê∏ÖÈô§', icon: 'success' });
      }
    }
  });
};

// ÂÖ≥‰∫éÊàë‰ª¨
const gotoAbout = () => {
  uni.showModal({
    title: 'ÂÖ≥‰∫éÊàë‰ª¨',
    content: 'Â§ßÂ≠¶ÁîüËøêÂä®ÂÅ•Â∫∑ÁÆ°ÁêÜÁ≥ªÁªü v1.0.0\n‰∏ì‰∏∫ÂÖ¨ÂÆâÈô¢Ê†°ÂÆöÂà∂ÁöÑË∑ëÊ≠•ÊâìÂç°Â∑•ÂÖ∑',
    showCancel: false
  });
};

// ÈÄÄÂá∫ÁôªÂΩï
const logout = () => {
  uni.showModal({
    title: 'ÈÄÄÂá∫ÁôªÂΩï',
    content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÂΩìÂâçË¥¶Âè∑ÂêóÔºü',
    success: (res) => {
      if (res.confirm) {
        uni.clearStorageSync();
        uni.reLaunch({ url: '/pages/login/login' });
      }
    }
  });
};
</script>

